#thumbnailbox{
    display:inline-block;
    margin:16px
}
#thumbnail{
    width:180px;
    height:180px;
    border-radius:4px;
    object-fit:contain
}
#info{
    width:70%;
    display:inline-block;
    vertical-align:top;
    position:relative;
    top:32px    
}
#pricecity{
    width: 100%;
    display:inline-block;
    margin-bottom:32px
}
#priceship{
    width:80%;
    display:inline-block;
    margin:none
}
#precio{
    color: #333333;
    font-size:24px;
    display:inline-block;
    margin:0
 }
 #truck{
    height:17px;
    padding-left:10px
 }

#ciudad{
    color:#666666;
    font-size:12px;
    display:inline-block;
    margin:0
}

#nombre{
    color: #333333;
    font-size:18px;
    width:50%;
    margin:0
}

a:link{
    text-decoration:none
}
#grayline{
    height:1px;
    width:95%;
    margin-left:auto;
    margin-right:auto;
    background-color:#eeeeee
}