#buscador{
    background-color:#FFE600;
}
#navegacion{
    width:972px;
    margin-left:auto;
    margin-right:auto
}
#logobox{
    display:inline-block;
    line-height:0;
    vertical-align:middle;
    margin-right:1%;
    max-width:5%
}
#logo{
    display:block;
    width:100%;
    padding:8%;
    box-sizing: border-box;
}
#formsearch{
    display:inline-block;
    width:94%
}
#search{
    border-style:none;
    color: #999999;
    height:27.73px;
    width:95%;
    padding-left:12px;
    box-sizing:border-box;
    display:inline-block;
}
#lupa{
    width:5%;
    border-style:none;
    height:27.73px;
    box-sizing:border-box;
    background-color:#eeeeee;
    background-image: url("../assets/lupa.png");
    background-repeat: no-repeat;
    background-position:center;
    background-size:30%;
    display:inline-block;
    vertical-align:bottom;
}

