#container{
   vertical-align:center;
   width:972px;
   margin-left:auto;
   margin-right:auto;
   background-color:white
}
#cajaproducto{
    display:inline-block;
    width:70%;
    margin-left:32px;
    margin-right:16px;
}
#producto{
    width:680px;
    height:680px;
    margin-left:auto;
    margin-right:auto;
    margin-top:32px;
    object-fit:contain
}
#comprabox{
    width:25%;
    margin-left:auto;
    display:inline-block;
    vertical-align:top;
    padding-right:32px;
    box-sizing: border-box;
}
#remain{
    margin-top:32px;
    margin-bottom:16px;
    font-size:14px;
    color:#333333;
}
#remain p{
    display:inline-block;
    white-space: pre-wrap;
}
#title{
    font-size:24px;
    margin-top:16px;
    width:75%
}
#price{
    font-size:46px;
    margin-top:32px;
    margin-bottom:32px;
    color:#333333;
}
#compra{
    background-color:#3483FA;
    box-sizing:border-box;
    width:100%;
    border-style:none;
    border-radius:4px;
    height:40px;
    color:white
}
#compra:active{
    background-color:#eeeeee;
    color:#3483FA
}
#infoprod {
    width:58.3%;
    margin-left:32px
}
#subtitle{
    font-size:28px;
    color:#333333;
    margin-bottom:32px;
}
#description{
    font-size:16px;
    color:#999999;
    padding-bottom:32px;
    box-sizing:border-box
}